import React, { Component } from "react";

class Current extends Component {
  state = {};

  renderCurrent = () => {
    const { cityName, timestamp, current } = this.props;
    const { IM, ME, sunset, sunrise } = current;

    const iconURL = `http://openweathermap.org/img/w/${IM.icon}.png`;
    const bgImage = `../data/images/cloudy.jpg`;

    return (
      <React.Fragment>
        <div className="d-flex flex-column align-items-center card-header bg-opacity-2 my-1">
          <h1 className="text-left">{cityName}</h1>
          <h5 style={{ margin: "2%" }}>{timestamp}</h5>
          <div className="d-flex align-items-center">
            <img src={iconURL} className="img-fluid" alt="Responsive"></img>
            <h2 className="card-text text-left">{IM.description}</h2>
          </div>

          <div className="d-flex flex-wrap">
            <h1 className="card-text text-left">{IM.temp}</h1>
            <p className="card-text text-left">˚F</p>

            <h1 className="card-text text-left"> / {ME.temp}</h1>
            <p className="card-text text-left">˚C</p>
          </div>
        </div>

        <div className="card-body bg-opacity-2 my-1">
          <div className="row">
            <div className="col-4 py-1 my-1 px-0 mx-0 text-center">
              <h4 className="card-text">Feels like</h4>
              <div className="d-flex flex-wrap justify-content-center">
                <h4>{IM.feels_like}</h4>
                <p className="card-text">˚F</p>
                <h4>/ {ME.feels_like}</h4>
                <p className="card-text">˚C</p>
              </div>
            </div>

            <div className="col-4 py-1 my-1 px-0 mx-0 text-center">
              {" "}
              <h4 className="card-text">Humidity</h4>
              <h4 className="card-text">{IM.humidity}%</h4>
            </div>

            <div className="col-4 py-1 my-1 px-0 mx-0 text-center">
              <h4 className="card-text">Wind</h4>
              <div className="d-flex flex-wrap justify-content-center">
                <h4 className="card-text text-left">{IM.wind_speed}</h4>
                <p className="card-text text-left">mph</p>
                <h4 className="card-text text-left">/ {ME.wind_speed}</h4>
                <p className="card-text text-left">kmh</p>
              </div>
            </div>
          </div>
          <hr />
          <div className="row">
            <div className="col sm-6 text-right">
              <h4 className="card-text">Sunrise: {sunrise}</h4>
            </div>
            <div className="col sm-6 text-left">
              <h4 className="card-text">Sunset: {sunset}</h4>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  };

  render() {
    return <React.Fragment>{this.renderCurrent()}</React.Fragment>;
  }
}

export default Current;

