import dayjs from "dayjs";

var utc = require("dayjs/plugin/utc");
dayjs.extend(utc);
let timezone_offset;

// return object of selected hourly data
function saveHourlyData(array_IM, array_ME) {
  let data_IM = [];
  let data_ME = [];

  for (let i = 0; i < array_IM.length; i++) {
    let obj_IM = {};

    obj_IM.time = dayjs
      .utc((array_IM[i].dt + timezone_offset) * 1000)
      .format("M/D ha");

    obj_IM.dt = array_IM[i].dt;
    obj_IM.temp = Math.round(array_IM[i].temp);
    obj_IM.icon = array_IM[i].weather[0].icon;
    data_IM.push(obj_IM);
  }

  for (let i = 0; i < array_ME.length; i++) {
    let obj_ME = {};

    obj_ME.dt = array_ME[i].dt;
    obj_ME.temp = Math.round(array_ME[i].temp);
    obj_ME.icon = array_ME[i].weather[0].icon;
    data_ME.push(obj_ME);
  }

  let data = {
    ME: data_ME,
    IM: data_IM,
  };

  return data;
}

// return object of selected daily data
function saveDailyData(array_IM, array_ME) {
  let data_IM = [];
  let data_ME = [];

  for (let i = 0; i < array_IM.length; i++) {
    let obj_IM = {};

    obj_IM.time = dayjs
      .utc((array_IM[i].dt + timezone_offset) * 1000)
      .format("M/D");

    obj_IM.temp_max = Math.round(array_IM[i].temp.max);
    obj_IM.temp_min = Math.round(array_IM[i].temp.min);
    obj_IM.icon = array_IM[i].weather[0].icon;
    obj_IM.uvi = Math.round(array_IM[i].uvi * 10) / 10;
    obj_IM.rain = array_IM[i].rain
      ? Math.round(array_IM[i].rain * 10) / 10
      : null;
    obj_IM.snow = array_IM[i].snow
      ? Math.round(array_IM[i].snow * 10) / 10
      : null;
    data_IM.push(obj_IM);
  }

  for (let i = 0; i < array_ME.length; i++) {
    let obj_ME = {};

    obj_ME.dt = array_ME[i].dt;
    obj_ME.temp_max = Math.round(array_ME[i].temp.max);
    obj_ME.temp_min = Math.round(array_ME[i].temp.min);
    obj_ME.icon = array_ME[i].weather[0].icon;

    obj_ME.uvi = Math.round(array_ME[i].uvi * 10) / 10;
    data_ME.push(obj_ME);
  }

  let data = {
    ME: data_ME,
    IM: data_IM,
  };

  return data;
}

//
function saveCurrentData(current_IM, current_ME) {
  let data_IM = {};
  let data_ME = {};
  timezone_offset = current_IM.timezone_offset;
  current_IM = current_IM.current;
  current_ME = current_ME.current;

  data_IM.temp = Math.round(current_IM.temp);
  data_IM.feels_like = Math.round(current_IM.feels_like);
  data_IM.wind_speed = Math.round(current_IM.wind_speed * 10) / 10;
  data_IM.description = current_IM.weather[0].description;
  data_IM.icon = current_IM.weather[0].icon;
  data_IM.humidity = current_IM.humidity;
  data_IM.uvi = current_IM.uvi;

  data_ME.temp = Math.round(current_ME.temp);
  data_ME.feels_like = Math.round(current_ME.feels_like);
  data_ME.wind_speed = Math.round(current_ME.wind_speed * 10) / 10;

  var utc = require("dayjs/plugin/utc");
  dayjs.extend(utc);
  const sunrise = dayjs
    .utc((current_IM.sunrise + timezone_offset) * 1000)
    .format("h:mma");
  const sunset = dayjs
    .utc((current_IM.sunset + timezone_offset) * 1000)
    .format("h:mma");

  let data = {
    ME: data_ME,
    IM: data_IM,
    sunrise,
    sunset,
  };

  return data;
}

export { saveHourlyData, saveDailyData, saveCurrentData };
