import React, { Component } from "react";

class Hourly extends Component {
  state = {};

  renderHourly = () => {
    const { IM, ME } = this.props;

    let jsx = [];
    let iconURL = "";

    for (let i = 0; i < IM.length; i++) {
      iconURL = `http://openweathermap.org/img/w/${IM[i].icon}.png`;
      jsx.push(
        <div key={i} className="py-1 my-1 px-1 mx-1">
          <h6 className="py-0 my-0">{IM[i].time}</h6>
          <div className="weather-icon py-0 my-0">
            <img src={iconURL} className="img-fluid" alt="Responsive"></img>
          </div>

          <h6 className="py-0 my-0">{IM[i].temp}˚F</h6>

          <h6 className="py-0 my-0">{ME[i].temp}˚C</h6>
        </div>
      );
    }

    return <div className="scrollmenu bg-opacity-5 my-1">{jsx}</div>;
  };

  render() {
    return <React.Fragment>{this.renderHourly()}</React.Fragment>;
  }
}

export default Hourly;
