import React, { Component } from "react";
import raindrop from "../data/raindrop.png";

class Daily extends Component {
  state = {};

  renderDaily = () => {
    const { IM, ME } = this.props;

    let iconURL;
    let jsx = [];

    for (let i = 0; i < IM.length; i++) {
      iconURL = `http://openweathermap.org/img/w/${IM[i].icon}.png`;
      jsx.push(
        <React.Fragment key={i}>
          <div className="d-flex row align-items-center py-0 my-0">
            <div className="col-1.5 daily-day py-0 my-0 px-1 mx-1">
              <h5>{IM[i].time}</h5>
            </div>
            <div className="col-2 py-0 my-0 px-1 mx-1">
              <img src={iconURL} className="img-fluid" alt="icon" />
            </div>
            <div className="col-2 py-0 my-0 px-1 mx-1">
              {IM[i].rain ? (
                <div className="d-flex align-items-center">
                  <img
                    src={raindrop}
                    className="img-fluid rain-icon"
                    alt="icon"
                  />
                  <h5 className="my-0">{IM[i].rain}in</h5>
                </div>
              ) : null}
            </div>

            <div className="col-2 py-0 my-0 px-1 mx-1">
              {IM[i].snow ? <h5>{IM[i].snow}in</h5> : null}
            </div>
            <div className="col-2 py-0 my-0 px-0 mx-0">
              <h5>{IM[i].temp_max}˚F</h5>
              <h5>{ME[i].temp_max}˚C</h5>
            </div>
            <div className="text-black-50 col-2 py-0 my-0 px-0 mx-0">
              <h5>{IM[i].temp_min}˚F</h5>
              <h5>{ME[i].temp_min}˚C</h5>
            </div>
          </div>
          <hr className="py-0 my-1 px-0 mx-0" />
        </React.Fragment>
      );
    }

    return <div>{jsx}</div>;
  };

  render() {
    return (
      <React.Fragment>
        <div className="container bg-opacity-5">{this.renderDaily()}</div>
      </React.Fragment>
    );
  }
}

export default Daily;
